import { getDefaultOptions, request } from './helpers';

export const richiestaPostFromForm = async (serviceUrl, richiesta) => {
  return post(`${serviceUrl}/api`, richiesta);
};

const get = url => {
  const options = {
    ...getDefaultOptions(),
    method: 'GET'
  };
  return request(url, options);
};

const post = (url, body) => {
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: JSON.stringify(body)
  };
  return request(url, options);
};
